const SCALE = 0.5;
const biomes = [
    createBiome("shallow", 4, 0, 0.5, 2, 0, 0, 2, 1, "rock-shallow", 1, "water-base", 0.5, 1),
    createBiome("kelp", 8, 40, 0.25, 0, 2, 2, 3, 2, "rock-kelp", 4, "water-kelp", 0.5, 0.5),
    createBiome("redgrass", 8, 90, 0.75, 0, 2, 1, 1, 0.5, "rock-medium", 2, "water-redgrass", 0.75, 2),
    createBiome("koosh", 6, 150, 0.5, 1, 4, 2, 3, 1, "rock-deep", 3, "water-koosh", 2, 1),
    createBiome("jellyshroom", 8, 200, 0, 0, 2, 1, 4, 1, "rock-scale", 1, "water-jelly", 1.5, 2),
    createBiome("grandreef", 10, 300, 0.5, 3, 4, 2, 6, 2, "rock-deep", 3, "water-greef", 1, 1.5),
    createBiome("bloodkelp", 8, 450, 0.75, 1, 4, 2, 1, 1, "rock-scale", 1, "water-bkelp", 1.5, 1.25),
    createBiome("lostriver", 12, 700, 0, 0, 3, 1, 2, 1, "rock-lr", 2, "water-lr", 1, 1),
    //mix into lr, using the dead space, createBiome("cove", 10, 900, 0, 0, 1, 1, 3, 1, "rock-lr", 2, "water-cove", 2, 0.5), //has custom code
    createBiome("lava", 15, 1000, 0, 0, 1, 1, 0, 0, "lava", 6, "water-ilz", 2, 0.25),
    createBiome("void", 1, 1400, 0, 0, 0, 0, 0, 0, "void", 3, "none", 0, 0),
];
function createBiome(name, rows, d, ff, minF, varCAmt, varCRate, varFAmt, varFRate, tex, vars, water, ores, plants) {
    const h = { rowsRequired: rows, floorFraction: ff, minFloorRows: minF };
    const vc = { rate: varCRate, range: varCAmt };
    const vf = { rate: varFRate, range: varFAmt };
    return { name: name, height: h, variationCeil: vc, variationFloor: vf, depth: d, baseTerrainTexture: tex, terrainVariants: vars, waterColor: water, oreSpawnRate: ores, plantSpawnRate: plants };
}
import { getRandomBetween, getRandomDecimalBetween, getArrayIndex, fitBCG, isDarkModeEnabled, setRandomRotations, setRotations, mirrorXs } from '../library';
import { isPhone } from '../browser';
import * as SimplexNoise from 'simplex-noise';
const simplex = new SimplexNoise();
function allocateBiomes(rows, cols) {
    const available = [];
    let rowRem = rows;
    let totalMin = 0;
    let idx = 0;
    for (const biome of biomes) {
        if (biome.height.rowsRequired <= rowRem) {
            available.push({ biome: biome, index: idx, height: new Array(cols), highest: 9999, lowest: -1 });
            rowRem -= biome.height.rowsRequired;
            totalMin += biome.height.rowsRequired;
            idx++;
        }
        if (rowRem <= 0)
            break;
    }
    //console.log("Available biome set is:");
    //console.log(available);
    //const maxD = available[available.length-1].biome.depth;
    const fac = rows / totalMin;
    for (const slot of available) {
        const amt = Math.floor(fac * slot.biome.height.rowsRequired);
        const ceilPoint = slot.index == 0 ? 0 : available[slot.index - 1].lowest + 1;
        const baseF = ceilPoint + amt;
        const floorVarFromOther = slot.index == available.length - 1 ? { rate: 0, range: 0 } : available[slot.index + 1].biome.variationCeil;
        const maxs = Math.min(3, slot.biome.variationFloor.rate, floorVarFromOther.rate);
        const maxr = Math.min(20, amt * 0.5 * (1 - slot.biome.height.floorFraction), slot.biome.variationFloor.range, floorVarFromOther.range);
        const floor = getSurface(cols, maxs, baseF - maxr, baseF + maxr);
        for (let x = 0; x < cols; x++) {
            const col = { biome: slot, topRow: 0, bottomRow: 0, rowsAllocated: 0, airCeiling: 0, airFloor: 0, solidFloor: 0, x: 0 };
            slot.height[x] = col;
            //const amt = Math.floor(fac*slot.biome.rowsRequired)+offsets[x];
            col.topRow = slot.index == 0 ? 0 : available[slot.index - 1].height[x].bottomRow + 1;
            col.airCeiling = col.topRow;
            col.airFloor = col.bottomRow;
            col.solidFloor = col.bottomRow;
            col.bottomRow = floor[x]; //slot.topRow[x]+slot.rowsAllocated[x]-1;
            col.rowsAllocated = col.bottomRow - col.topRow + 1; //slot.topRow[x]+slot.rowsAllocated[x]-1;
            slot.highest = Math.min(slot.highest, col.topRow);
            slot.lowest = Math.max(slot.lowest, col.bottomRow);
        }
    }
    while (available[available.length - 1].highest >= rows) {
        available.pop();
    }
    available[available.length - 1].lowest = rows - 1;
    for (let x = 0; x < cols; x++) {
        available[available.length - 1].height[x].bottomRow = rows - 1;
    }
    console.log("Calculated biome stack is:");
    console.log(available);
    return available;
}
function getBiomeAtRow(row, biomes, x) {
    if (biomes.length == 1 || row < biomes[1].height[x].topRow)
        return biomes[0];
    if (row >= biomes[biomes.length - 1].height[x].topRow)
        return biomes[biomes.length - 1];
    for (const biome of biomes) {
        if (row >= biome.height[x].topRow && row <= biome.height[x].bottomRow)
            return biome;
    }
    console.log("No biome available at row=" + row + " of " + biomes + " @ " + x);
    return null;
}
function addLooseImage(elem, img, w, h) {
    let tile = document.createElement('div'); /*
    if (img == "air" || img == "empty" || img == null) {
        tile.style.width = w.toString()+"px";
        tile.style.height = h.toString()+"px";
    }*/
    tile.className = "tile-subnautica-" + img;
    elem.appendChild(tile);
    return tile;
}
function addPositionedImage(elem, img, x, y, w, h) {
    img = modifyImageLocational(img);
    if (!img || img == "air" || img == "empty")
        return null;
    let added = addLooseImage(elem, img, w, h);
    added.style.position = "relative";
    added.style.left = x.toString() + "px";
    added.style.top = y.toString() + "px";
    //if (img.startsWith("geothermal"))
    //	scale *= 2;
    added.style.transformOrigin = "top left";
    //added.style.transform = "scale("+scale+")";
    //if (img == "spawner")
    //	added.style.transform = added.style.transform+" translate(-16px)";
    return added;
}
