function buildImage(grid, waterOverlay, rows, cols, tiles) {
    var w = 128 * SCALE;
    var h = 128 * SCALE;
    var x = 0;
    var y = 0;
    for (var k = 0; k < rows; k++) {
        x = 0;
        let tilerow = document.createElement("div");
        let waterrow = document.createElement("div");
        for (var i = 0; i < cols; i++) {
            let idx = getArrayIndex(cols, i, k);
            const tile = tiles[idx];
            let addTile = tilerow;
            let addWater = waterrow;
            let tileTex = modifyImageLocational(tile.baseTexture);
            if (!tileTex)
                tileTex = "empty";
            addTile = addImageToRow(addTile, tileTex, 0);
            if (isDarkModeEnabled()) {
                //add = addImageToRow(add, "dark");
                if ((!tile.backgroundWater || tile.backgroundWater == "empty") && (!tile.tileOverlay || tile.tileOverlay == "empty"))
                    tile.tileOverlay = "cave"; //tile.biome.biome.waterColor;
            }
            if (tile.backgroundWater) {
                tile.backgroundWater = modifyImageLocational(tile.backgroundWater);
                if (tile.backgroundWater)
                    addTile = addImageToRow(addTile, tile.backgroundWater, 1);
            }
            let flag = false;
            if (tile.tileOverlay) {
                tile.tileOverlay = modifyImageLocational(tile.tileOverlay);
                if (tile.tileOverlay) {
                    addWater = addImageToRow(addWater, tile.tileOverlay, 4);
                    flag = true;
                }
            }
            if (!flag)
                addWater = addImageToRow(addWater, "empty", 4);
            x += w;
        }
        grid.appendChild(tilerow);
        waterOverlay.appendChild(waterrow);
        y += h;
    }
    fitBCG(grid, SCALE);
    fitBCG(waterOverlay, SCALE);
}
function getRandomBiomeTerrain(x, y, biome) {
    const f = (y - biome.height[x].topRow) / biome.height[x].rowsAllocated;
    const bottom = y >= biome.height[x].bottomRow + 1 - (1 / SCALE);
    if (biome.biome.name == "bloodkelp" && bottom)
        return "bkelp-floor";
    let tex = biome.biome.baseTerrainTexture;
    const noise = ((1 + simplex.noise2D(x * 0.1, y * 0.1)) / 2);
    let variant = 1 + Math.floor(noise * biome.biome.terrainVariants);
    if (biome.biome.name == "lostriver") {
        if (f < 0.2) {
            tex = "lr";
            variant = 4;
        }
        else if (f < 0.3) {
            tex = "lr";
            variant = 3;
        }
    }
    else if (biome.biome.name == "cove") {
        tex = "lr"; /*
        if (bottom) {
            variant = 1+Math.floor(noise*2);
        }
        else */
        if (f < 0.25) {
            variant = 8;
        }
        else if (f < 0.9975) {
            variant = 7;
        }
        else {
            variant = 6;
        }
    }
    return tex + variant;
}
function modifyImageLocational(img) {
    if (isDarkModeEnabled()) {
        if (img && (img.includes("water-") || img.includes("cave-") || img.includes("brine-"))) {
            img = "dark-" + img;
        }
        else if (img && img.endsWith("-glow_toggle")) {
            img = null;
        }
    }
    return img;
}
