function genBlob(rows, cols, tiles, x, y, type, s, exclude, store) {
    const rx = getRandomDecimalBetween(s[0], s[1]);
    const ry = getRandomDecimalBetween(s[0], s[1]);
    const mx = Math.max(0, Math.floor(x - rx));
    const my = Math.max(0, Math.floor(y - ry));
    const px = Math.min(cols - 1, Math.floor(x + rx));
    const py = Math.min(rows - 1, Math.floor(y + ry));
    const ms = 0.95;
    const slopeX = getRandomDecimalBetween(-ms, ms);
    const slopeY = getRandomDecimalBetween(-ms, ms);
    for (let i = mx; i <= px; i++) {
        for (let k = my; k <= py; k++) {
            const di = i - x;
            const dk = k - y;
            if (di * di + dk * dk <= rx * ry + 0.5) {
                const dx = i + Math.floor(slopeX * dk);
                const dy = k + Math.floor(slopeY * di);
                if (dx < 0 || dy < 0 || dx >= cols || dy >= rows)
                    continue;
                const idx = getArrayIndex(cols, dx, dy);
                if (idx >= 0 && idx < tiles.length && !(exclude && exclude(idx)) && !store.includes(idx)) {
                    tiles[idx].backgroundWater = type;
                    store.push(idx);
                    if (type.includes("water") || type.includes("cave"))
                        tiles[idx].isAir = true;
                }
            }
        }
    }
}
function getSurface(cols, maxslope, top, bottom) {
    const ret = new Array(cols);
    let lastY = -1;
    for (let x = 0; x < cols; x++) {
        let y = getRandomBetween(top, bottom);
        const diff = y - lastY;
        if (lastY >= 0 && Math.abs(diff) > maxslope)
            y = lastY + Math.sign(diff);
        lastY = y;
        ret[x] = y;
    }
    return ret;
}
function generateBiomeFloor(cols, tiles, biome, maxslope, minfrac, minFloorRows) {
    const middle = getSurface(cols, biome.biome.variationFloor.rate, biome.highest + (biome.lowest - biome.highest) * minfrac, biome.lowest - 1 - minFloorRows);
    for (let x = 0; x < cols; x++) {
        const col = biome.height[x];
        //console.log(biome.biome.name+"floor @ "+x+": "+biome.topRow+" > "+bottom[x]+" ["+biome.topRow+"-"+col.bottomRow+"]");
        col.airFloor = Math.min(middle[x], col.bottomRow - 1);
        col.solidFloor = col.airFloor;
        if (biome.index <= 6 && biome.index != 4) { //shallows to bkelp, skip jellyshroom
            for (let y = col.topRow; y <= col.bottomRow; y++) {
                const idx = getArrayIndex(cols, x, y);
                tiles[idx].tileOverlay = biome.biome.waterColor;
                if (y <= col.airFloor) {
                    tiles[idx].backgroundWater = biome.biome.waterColor;
                    tiles[idx].isAir = true;
                }
            }
        }
    }
}
function addObject(objects, illum, obj, x, y, dx, dy) {
    return addObjectOfSizeAt(objects, illum, obj, x, y, 128, 128, dx, dy);
}
function addObjectOfSizeAt(objects, illum, obj, x, y, w, h, dx, dy) {
    x *= 128; //convert from tile to px
    y *= 128;
    if (dx)
        x += dx;
    if (dy)
        y += dy;
    let ret = [];
    if (obj.mainTexture)
        ret.push(addPositionedImage(objects, obj.mainTexture, x, y, w, h));
    if (obj.illumTexture)
        ret.push(addPositionedImage(illum, obj.illumTexture, x, y, w, h));
    return ret;
}
function generateTallPlant(cols, objects, illum, x, rootY, topY, baseTex, topTex, rootTex) {
    const dx = getRandomBetween(-32, 32);
    for (let y = topY; y <= rootY; y++) {
        const top = y == topY;
        const bottom = y == rootY;
        //const idx = getArrayIndex(cols, x, y);
        const arr = top ? topTex : (bottom && rootTex ? rootTex : baseTex);
        const obj = arr[getRandomBetween(0, arr.length - 1)];
        //addBlock(tiles, idx, obj);
        addObject(objects, illum, obj, x, y, dx);
    }
    //console.log("Plant @ "+x+", "+rootY+" > "+baseTex[0]);
}
function generateDoubleWidePlant(cols, objects, illum, x, rootY, topY, baseTex, middleTex, topTex) {
    const dx = getRandomBetween(-16, 16);
    for (let y = rootY; y >= topY; y--) {
        const top = y == topY;
        const bottom = y == rootY;
        const spr = top ? topTex : (bottom || !middleTex ? baseTex : middleTex);
        let str = spr.mainTexture ? spr.mainTexture : spr.illumTexture;
        if (str && str.charAt(str.length - 2) == '*') {
            const num = parseInt(str.charAt(str.length - 1));
            str = str.substring(0, str.length - 2);
            const isr = spr.illumTexture ? spr.illumTexture.substring(0, spr.illumTexture.indexOf('*')) + spr.illumTexture.substring(spr.illumTexture.indexOf('*') + 2) : null;
            //console.log(spr.mainTexture+"/"+spr.illumTexture+" > "+isr);
            for (let i = 0; i < num; i++) {
                let yy = y - i;
                //const idx1 = getArrayIndex(cols, x, yy);
                //const idx2 = getArrayIndex(cols, x+1, yy);
                //addBlock(tiles, idx1, createTextureWithSuffix(spr, str, (i+1).toString()+"-left"));
                //addBlock(tiles, idx2, createTextureWithSuffix(spr, str, (i+1).toString()+"-right"));
                addObject(objects, illum, createTextureWithSuffix(spr, str, isr, (i + 1).toString() + "-left"), x, yy, dx);
                addObject(objects, illum, createTextureWithSuffix(spr, str, isr, (i + 1).toString() + "-right"), x + 1, yy, dx);
            }
            y -= num - 1;
        }
        else {
            //const idx1 = getArrayIndex(cols, x, y);
            //const idx2 = getArrayIndex(cols, x+1, y);
            //addBlock(tiles, idx1, createTextureWithSuffix(spr, str, "-left"));
            //addBlock(tiles, idx2, createTextureWithSuffix(spr, str, "-right"));
            addObject(objects, illum, createTextureWithSuffix(spr, str, null, "-left"), x, y, dx);
            addObject(objects, illum, createTextureWithSuffix(spr, str, null, "-right"), x + 1, y, dx);
        }
    }
}
function generateCreepvine(rows, cols, tiles, biomes, objects, illum) {
    let biome = biomes[1];
    for (let x = 0; x < cols; x++) {
        if (getRandomBetween(0, 3) > 0) {
            const col = biome.height[x];
            const n = getRandomBetween(0, 6);
            let root = -1;
            let top = -1;
            switch (n) {
                case 0:
                case 1:
                case 2:
                    root = col.solidFloor;
                    top = Math.max(col.topRow, root - getRandomBetween(4, 8));
                    generateTallPlant(cols, objects, illum, x, root, top, [{ mainTexture: "creepvine-1" }, { mainTexture: "creepvine-2" }, { mainTexture: "creepvine-3" }, { mainTexture: "creepvine-4" }, { mainTexture: "creepvine-5" }], [{ mainTexture: "creepvine-top1" }], null);
                    break;
                case 3:
                case 4:
                case 5:
                    if (x < cols - 1 && Math.abs(biome.height[x].solidFloor - biome.height[x + 1].solidFloor) <= 1) {
                        root = Math.max(biome.height[x].solidFloor, biome.height[x + 1].solidFloor);
                        top = Math.max(col.topRow, root - 3);
                        generateDoubleWidePlant(cols, objects, illum, x, root, top, { mainTexture: "large-creepvine-bottom*2" }, n == 5 ? createDualTexture("creepvine-seed") : { mainTexture: "large-creepvine-center" }, { mainTexture: "large-creepvine-top*3" });
                        x += 1;
                    }
                    break;
            }
        }
    }
}
function generateReefPods(rows, cols, tiles, biomes, objects, illum) {
    let biome = biomes[5];
    for (let x = 0; x < cols - 1; x++) {
        if (getRandomBetween(0, 3) > 0) {
            if (Math.abs(biome.height[x].solidFloor - biome.height[x + 1].solidFloor) <= 1) {
                let root = Math.max(biome.height[x].solidFloor, biome.height[x + 1].solidFloor);
                let top = Math.max(biome.height[x].topRow + 2, biome.height[x + 1].topRow + 2, root - getRandomBetween(2, 5));
                //generateTallPlant(cols, tiles, x, root, top, ["reef-pod-root"], ["reef-pod"]);
                generateDoubleWidePlant(cols, objects, illum, x, root, top, { mainTexture: "reef-pod-bottom*2" }, { mainTexture: "reef-pod-middle" }, createDualTexture("reef-pod-top*2"));
                x += 2;
            }
        }
    }
}
function generateBloodKelp(rows, cols, tiles, biomes, objects, illum) {
    let biome = biomes[6];
    for (let x = 0; x < cols; x++) {
        if (getRandomBetween(0, 1) > 0) {
            let root = biome.height[x].solidFloor;
            let top = Math.max(biome.height[x].topRow, root - getRandomBetween(3, 12));
            generateTallPlant(cols, objects, illum, x, root, top, [{ mainTexture: "bkelp-middle1" }, { mainTexture: "bkelp-middle2" }, { mainTexture: "bkelp-middle3" }], [{ mainTexture: "bkelp-top1" }, { mainTexture: "bkelp-top2" }], [createDualTexture("bkelp-bottom1"), createDualTexture("bkelp-bottom2")]);
        }
    }
}
function generateKoosh(rows, cols, tiles, biomes, objects, illum) {
    let biome = biomes[3];
    for (let x = 0; x < cols - 1; x++) {
        if (getRandomBetween(0, 4) > 0) {
            if (Math.abs(biome.height[x].solidFloor - biome.height[x + 1].solidFloor) <= 1) {
                let root = Math.max(biome.height[x].solidFloor, biome.height[x + 1].solidFloor);
                generateDoubleWidePlant(cols, objects, illum, x, root, root - 1, createDualTexture("koosh-bottom"), null, createDualTexture("koosh-top"));
                tiles[getArrayIndex(cols, x, root)].groundObjects = 2;
                tiles[getArrayIndex(cols, x + 1, root)].groundObjects = 2;
                tiles[getArrayIndex(cols, x, root - 1)].groundObjects = 2;
                tiles[getArrayIndex(cols, x + 1, root - 1)].groundObjects = 2;
                x += 2;
            }
        }
    }
}
function createTextureWithSuffix(tex, basename, baseillum, suffix) {
    const start = basename ? basename : tex.mainTexture;
    let illum = tex.illumTexture ? (baseillum ? baseillum : tex.illumTexture) + suffix : null;
    let ret = { mainTexture: start + suffix, illumTexture: illum };
    return ret;
}
function createDualTexture(tex) {
    let ret = { mainTexture: tex, illumTexture: tex + "-glow" };
    return ret;
}
function createFullGlowTexture(tex) {
    let ret = { mainTexture: null, illumTexture: tex };
    return ret;
}
function generateGroundDeco(rows, cols, tiles, biomes, lrdata, objects, illum) {
    for (const biome of biomes) {
        const nPlant = 12 * biome.biome.plantSpawnRate;
        const nOre = 6 * biome.biome.oreSpawnRate;
        for (let i = 0; i < nPlant; i++) {
            const gen = getRandomPlant(biome);
            if (gen) {
                let x = getRandomBetween(0, cols - 1);
                if (lrdata && biome.index == 7 && Math.abs(x - lrdata.shaftLocation) <= 2)
                    continue;
                let y = Math.min(biome.height[x].solidFloor, rows - 1);
                const idx = getArrayIndex(cols, x, y);
                if (tiles[idx].groundObjects >= 1)
                    continue;
                if (gen.mainTexture == "wigglegrass" || gen.mainTexture == "acidmushroom" || gen.mainTexture == "whitemushroom") {
                    const n = getRandomBetween(2, 5);
                    for (let k = 0; k < n; k++)
                        addObject(objects, illum, gen, x, y, getRandomBetween(-60, 60));
                }
                else {
                    const varX = gen.mainTexture != "membrain" ? 48 : 24;
                    //addBlock(tiles, idx, gen, getRandomBetween(-varX, varX), getRandomBetween(2, 12));
                    addObject(objects, illum, gen, x, y, getRandomBetween(-varX, varX));
                }
                tiles[idx].groundObjects++;
            }
        }
        for (let i = 0; i < nOre; i++) {
            const gen = getRandomOre(biome);
            if (gen) {
                let x = getRandomBetween(0, cols - 1);
                if (lrdata && biome.index == 7 && Math.abs(x - lrdata.shaftLocation) <= 2)
                    continue;
                let y = Math.min(biome.height[x].solidFloor, rows - 1);
                const idx = getArrayIndex(cols, x, y);
                if (tiles[idx].groundObjects >= 1)
                    continue;
                const varX = gen.illumTexture == "abysso" || gen.mainTexture == "azurite" ? 48 : 24;
                //addBlock(tiles, idx, gen, getRandomBetween(-varX, varX), getRandomBetween(2, 12));
                addObject(objects, illum, gen, x, y, getRandomBetween(-varX, varX));
                tiles[idx].groundObjects++;
            }
        }
    }
    if (biomes.length <= 7 || !lrdata)
        return;
    for (let x = 0; x < cols; x++) {
        if (lrdata.coveFloor[x] >= 0 && getRandomBetween(0, 3) == 0 && Math.abs(lrdata.treeX - x) > 1) {
            const gen = getRandomOre(biomes[7]);
            if (gen) {
                const y = lrdata.coveFloor[x];
                const idx = getArrayIndex(cols, x, y);
                if (tiles[idx].groundObjects >= 1)
                    continue;
                const varX = gen.illumTexture == "abysso" || gen.mainTexture == "azurite" ? 48 : 24;
                addObject(objects, illum, gen, x, y, getRandomBetween(-varX, varX));
                tiles[idx].groundObjects++;
            }
        }
    }
}
function getRandomOre(biome) {
    let li = [{ mainTexture: "quartz" }];
    if (biome.index >= 3 && biome.index <= 7) {
        li.push({ mainTexture: "lithium" });
    }
    if (biome.index >= 3 && biome.index != 4 && biome.index <= 6) {
        li.push(createDualTexture("azurite"));
    }
    if (biome.index >= 4 && biome.index <= 7) {
        li.push({ mainTexture: "magnetite" });
    }
    if (biome.index >= 5 && biome.index <= 7) {
        li.push(createDualTexture("ruby"));
    }
    if (biome.index >= 6 && biome.index <= 7) {
        li.push(createDualTexture("uranium"));
    }
    if (biome.index == 6) {
        li.push(createFullGlowTexture("abysso"));
    }
    if (biome.index == 7) {
        li.push({ mainTexture: "sulfur" });
    }
    if (biome.index >= 7) {
        li.push({ mainTexture: "nickel" });
    }
    if (biome.index >= 8) {
        li.push(createFullGlowTexture("kyanite"));
    }
    return li.length == 0 ? null : li[getRandomBetween(0, li.length - 1)];
}
function getRandomPlant(biome) {
    let li = [];
    if (biome.index == 0) {
        li.push(createDualTexture("wigglegrass"));
        li.push(createDualTexture("acidmushroom"));
    }
    if (biome.index >= 2 && biome.index <= 3) {
        li.push({ mainTexture: "redwort" });
    }
    if (biome.index >= 2 && biome.index <= 5 && biome.index != 4) {
        li.push(createDualTexture("alkaliplant"));
    }
    if ((biome.index >= 4 && biome.index <= 5) || biome.index == 2) {
        li.push(createFullGlowTexture("violetbeau"));
    }
    if (biome.index >= 5 && biome.index <= 7) {
        li.push(createDualTexture("gabefeather"));
    }
    if (biome.index == 5) { //grand reef only
        li.push(createDualTexture("membrain"));
    }
    if (biome.index >= 6) {
        li.push({ mainTexture: "whitemushroom" });
    }
    if (biome.index == 7) { //lost river only
        li.push(createDualTexture("ghostweed"));
        li.push(createDualTexture("crabkelp"));
    }
    return li.length == 0 ? null : li[getRandomBetween(0, li.length - 1)];
}
